body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --hover-color: #00acdc;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.header {
  padding: 0 50px;
}

.container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.navigation, .nav-list {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav-link {
  text-align: center;
  color: #000;
  text-transform: uppercase;
  height: 15px;
  padding: 23px 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav-link:hover {
  color: var(--hover-color);
}

.nav-item {
  margin-right: 20px;
}

.nav-item:last-child {
  margin-right: 0;
}

.nav-link.is-active {
  color: var(--hover-color);
}

.nav-link.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 2px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 1px;
}

